@import "https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.head {
  justify-content: space-between;
  display: flex;
}

.button-head {
  border-radius: 3px;
  width: 3.6rem;
  height: 3.6rem;
  box-shadow: inset 3px 3px 10px #b1b1b198, inset -3px -3px 7px #fffefe;
}

.button-head i {
  padding: 1rem;
  font-size: 1.6rem;
}

h1 {
  margin-top: 20px;
  font-size: 1rem;
  font-weight: 800;
}

img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin: 1.2rem auto auto;
  display: block;
  box-shadow: -4px 4px 7px gray, 4px -4px 7px #ffffffcd;
}

main {
  background-color: #ebebeb;
  border-radius: 10px;
  width: 320px;
  margin: 2rem auto auto;
  padding: 2rem;
  box-shadow: 15px 18px 20px #8383837d;
}

h1, h2, h3 {
  font-family: Inter, sans-serif;
}

h2, h3 {
  text-align: center;
  margin-top: 1.1rem;
}

h2 {
  font-weight: 400;
}

h3 {
  color: gray;
  font-size: 1.4rem;
  font-weight: 800;
}

.line {
  background-color: #fff;
  border: 1px solid #80808074;
  border-radius: 10px;
  width: 90%;
  height: 10px;
  margin: 2rem auto;
}

.line_blue {
  background: linear-gradient(90deg, #6adbf4 0%, #1f8dfb 35%, #4fe2ff 100%);
  border-radius: 9px 0 0 9px;
  width: 70%;
  height: 8px;
}

.bottom {
  justify-content: space-between;
  display: flex;
}

.button-bottom {
  border-radius: 15px;
  width: 3rem;
  height: 3rem;
  margin-bottom: 1.5rem;
  box-shadow: -3px 3px 10px #b3b3b398, 3px -3px 5px #ffffffcd;
}

.button-bottom:nth-of-type(2) {
  box-shadow: inset -3px 3px 10px #b3b3b372, inset 3px -3px 8px #fffffffe;
}

.button-bottom i {
  padding: .5rem;
  font-size: 2rem;
}

.fa-play {
  color: #4fe2ff;
}

.fa-forward, .fa-backward, .fa-magnifying-glass {
  color: #504f4f;
}

.fa-power-off {
  color: red;
}

.gray-line {
  background-color: #fff;
  border-radius: 5px;
  width: 60%;
  height: 6px;
  margin: 5px auto auto;
  box-shadow: inset -1.3px 1.4px 3px #4e4e4eb4;
}

